@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);

/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
    display:block;
}

nav ul {
    list-style:none;
}

blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    font-size:100%;
    vertical-align:baseline;
    background:transparent;
}

/* change colours to suit your needs */
ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}

/* change colours to suit your needs */
mark {
    background-color:#ff9;
    color:#000;
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;  
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

input, select {
    vertical-align:middle;
}
*{
	z-index: 0;
}

html{ 
	background: url(https://media.giphy.com/media/SZ59wzAQnCzlu/giphy.gif) no-repeat center center fixed;
	background-size: cover;
	height: 100%;
    overflow: hidden;
	background-color: inherit !important;
	font-family: 'Open Sans', sans-serif;
	color: black;
}
.bg{
	background: url("https://media.giphy.com/media/SZ59wzAQnCzlu/giphy.gif") center no-repeat;
	background-size: cover;
	height: 100%;
}
.info{
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-around;
	background: #639ce7;
	padding: 16px 0;
	color: #fff;
	font-weight: bold;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	text-transform: uppercase;
	text-align: center;
}
.logo{
	width: 50px;
	height:50px;
	border-radius: 50%;
}
.barner{
	background-size: cover;
	background-image: url("https://media.giphy.com/media/SZ59wzAQnCzlu/giphy.gif");
}
.center-text{
	text-align: center;
}
a{
	text-decoration: none;
	color: #fff;
}
i{
	height: 100%;
}
h1{
	font-family: san-serif;
}
img{
	width: 100%;
}
.information{
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-around;
}
.footer{
	display: flex;
	flex-direction: row;
	align-items: stretch;
	justify-content: space-around;
	padding: 32px 0;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	background: #fff;
	box-shadow: -5px -5px 5px 5px #888;
	
}

@media only screen and (max-width : 640px){
	h1{
		/*letter-spacing: normal;*/
	}
	
}
.signup, .signin{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
}
input{
	margin-top: .5em;
	display: flex;
	flex-direction: column;
	align-items: center;
}
@media only screen and (min-width : 769px){
	.info{
		background: transparent;
	}
}
